import {NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes : Routes = [
    {
        path : 'coprate-user',
        loadChildren : () => import('./coprate-user/coprate-user.module').then(m=>m.CoprateUserModule),
        data : {title : 'CoprateUser'}
    }
     
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class CoprateMgmtRoutingModule{}